:root {
  --yellow-color: #ffe550;
  --light-green-color: #bbf330;
  --dark-green-color: #7eb101;
  --white-color: #fff;
  --black-color: #2c2c2c;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  color: var(--black-color);
  font-family: Roboto, Regular, sans-serif;
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
}

img {
  display: inline-block;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

